<template>
    <theContain
    v-if="isChange"
    :brunch="brunch"
    />
</template>
<script>
import theContain from './theContain.vue'

import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

import { ref,computed,watch,nextTick,provide} from 'vue';
export default {
    components:{theContain},
    setup(){
        const store=useStore()
        let brunches=computed(()=>{
            return store.state.brunches.brunches
        })

        const router=useRouter()
        
        let nowpath=computed(()=>{
            // 防止超链接标签改变fullPath导致nowpath无法与brunch.route匹配
            if(router.currentRoute.value.fullPath.indexOf('#')+1){
                let k=router.currentRoute.value.fullPath.indexOf('#')
                return router.currentRoute.value.fullPath.slice(0,k)
            }
            return router.currentRoute.value.fullPath
        })
        // 匹配nowpath与brunch.route
        let brunch=computed(()=>{
            let b
            brunches.value.forEach(item=>{
            if(item.route===nowpath.value){
                b=item
            }})
            return b
        })
        provide('brunch',brunch)
        // 切换路由时重新挂载页面
        let isChange=ref(1)
        watch(nowpath,()=>{
            //返回页面顶部
            document.documentElement.scrollTop = 0
            isChange.value=0
            nextTick(()=>isChange.value=1)
        })
        return{
            brunch,
            isChange
        }
    }
}
</script>
