<template>
    <ul>
        <li v-for="(data,index) in datas" :key="index">
            <a v-if="data.title" 
            :ref="getLi"
            @click="isAct(data.title)"
            >
                {{ data.title }}
            </a>
            <rightNav v-if="data.sections" 
            :datas="data.sections"
            @getLi="getLi"
            :first="truelis"
            @getStyle="getStyle"/>

        </li>
    </ul>
</template>
<script>
import{ref,onUpdated,watch,nextTick}from 'vue'

import { useStore } from 'vuex'

import {spreadArr} from '../hooks/spreadArr'

export default {
    name:'rightNav',
    props:['datas','first'],
    setup(props,ctx){
        let lis=[]
        let getLi=e=>{
            lis.push(e)
        } 
        ctx.emit('getLi',lis)
        //title前添加井号并绑定给超链接的href
        let addPound=(data)=>{
            return '#'+data
        }
        const store=useStore()
        let bmaincolor=store.state.bmaincolor

        let p=props
        let truelis=[]
        // 第一次传入的first判断组件是不是第一次（还未嵌套才能获取完整li）
        //向后续组件传入的first为完整li（嵌套组件传入参数名只能为第一层组件的props中拥有的，所以不得不以first为名）
        //其实完全可以将theContain组件里的truelis传进来，但为了使该目录组件更为完整才出此下策
        if(p.first==1){
            onUpdated(()=>{
                spreadArr(lis,truelis)
            })
        }else {
            truelis=p.first
        }

        
        // li高
        let h='26px'
        let intH=parseInt(h)
        let display=ref('none')
        //切换路由时display置none
        store.commit('elevator/switch','none')
        // 监听store里的display
        watch(()=>store.state.elevator.display,(i)=>{
            display.value=i
            if(i=='none'){
                truelis.forEach((trueli)=>{
                // 排他
                trueli.className=''})
            }
        },{immediate:true})
        // 计算浮层margintop
        let mt=ref(0)
        let x=ref(0)
        function isAct(i){
            //scrollIntoview因为页面没渲染而出问题
            nextTick(()=>document.getElementById(i).scrollIntoView())
            truelis.forEach((trueli,k)=>{
                // 排他
                trueli.className=''
                if(trueli.innerHTML==i){
                    trueli.className='active'
                    //自动滚动目录功能的判断中有scrollTop==0的条件,导致初始跳转失败,借此完成初始跳转
                    if(p.first==1)trueli.scrollIntoView({block: "center"});
                    // 计算active上方有几个li
                    x.value=k
                    //令display为block
                    store.commit('elevator/switch','block')
                    mt.value=-(intH*(truelis.length-x.value))+'px'
                    getStyle=(x.value,display.value)
                }
                //判断是否为递归组件
                if(ctx.attrs.onGetStyle){
                    ctx.emit('getStyle',x.value,display.value)
                }
            })
        }
        // 自定义事件传入递归组件内使父组件可获得style同时作为递归组件的标识
        let getStyle=(x,d)=>{
            mt.value=-(intH*(truelis.length-x))+'px'
            display.value=d
            if(ctx.attrs.onGetStyle){
                ctx.emit('getStyle',x,d)
            }
        }

        //自动滚动目录
        let timer
        watch(mt,()=>{
            let rightnav=document.querySelector('.right-nav')//窗口元素
            let bigul=document.querySelector('.bigul')//大盒子
            let rh=rightnav.offsetHeight//窗口高度
            let bh=bigul.offsetHeight//盒子高度
            let mat=bigul.offsetHeight+parseInt(mt.value)//电梯到盒子顶部
            let st=rightnav.scrollTop//盒子被卷去的距离
            clearInterval(timer)
            if(mat-st>rh||mat<st){
                timer = setInterval(()=>{
                    rh=rightnav.offsetHeight
                    let target=Math.ceil(mat-rh/2)//窗口中间
                    // var step = Math.ceil((target - obj.offsetLeft) / 10);
                    let step = (target - rightnav.scrollTop) / 10;
                    step = step >0? Math.ceil(step) : Math.floor(step);
                    if (
                        Math.ceil(rightnav.scrollTop) == target||Math.floor(rightnav.scrollTop)== target||
                        Math.ceil(rightnav.scrollTop)>=bh-rh||Math.floor(rightnav.scrollTop)>=bh-rh||
                        Math.ceil(rightnav.scrollTop)==0||Math.floor(rightnav.scrollTop)==0)
                    {
                        //停止动画 本质是停止定时器
                        clearInterval(timer)
                    }
                    // 把每次加1 这个步长值改为一个慢慢变小的值 步长公式:(目标值 - 现在的位置) / 10
                    rightnav.scrollTop =rightnav.scrollTop+step;
                },15);
            }
        })

        return{
            getLi,
            addPound,
            isAct,
            mt,
            h,
            display,
            bmaincolor,
            truelis,
            getStyle,
        }
    }
}
</script>

<style lang="less" scoped>
@bmaincolor:v-bind(bmaincolor);
@h:v-bind(h);
@m-t:v-bind(mt);
@display:v-bind(display);
.bigul{
    width: 155px;
    background: rgba(255,255,255,.8);
    text-align: left;
    ul{
        padding-left: 10px;
    }
    li{
        
        width: 160px;
        padding-left: 5px;
        a{
            display: block;
            height: @h;
            color: rgb(95, 100, 107);
            line-height: 25px;
            font-size: 11px;
            overflow: hidden;
            white-space: nowrap; 
            text-overflow: ellipsis;
            transition: all .3s;
            cursor: pointer;
            &:hover{
                color:@bmaincolor;
            }
        }
    }
    &::after{
        content: '';
        float: left;
        display: @display;
        margin-top: @m-t;
        width: 120%;
        height: @h ;
        border-radius: 5px;
        background-color: @bmaincolor;
        transition: all .3s;
    }
    
    .active{
        color: white;
        &:hover{
            color:white;
        }
    }
}
</style>