<template>
<div class="text">
    <div v-for="(data,index) in datas" 
    :key="index">
        <h1 v-if="data.title" :id="data.title" :ref="getTitle" class="title">
            {{ data.title }}
        </h1>
        <span v-if="data.contents">
            <p v-for="(content,index) in data.contents" :key="index" class="content">
                {{ content }}
            </p>
        </span>
        <span v-if="data.imgs">
            <p v-for="(img,index) in data.imgs" :key="index">
                <img :src="require('../assets/img'+brunch.route+'/'+img+'.jpg')">
            </p>
        </span>
        <theContent v-if="data.sections" 
        :datas="data.sections" 
        @getTitle="getTitle"/>
    </div>
</div>
    
</template>
<script>
import {inject} from 'vue'
import {useStore} from 'vuex'
export default {
    name: "theContent",
    props: ["datas"],
    setup(p, ctx) {
        let brunch = inject("brunch");
        let titles = [];
        let getTitle = e => {
            titles.push(e);
        };
        ctx.emit("getTitle", titles);
        const store = useStore();
        let bmaincolor = store.state.bmaincolor;
        return {
            brunch,
            getTitle,
            bmaincolor,
        };
    },
}
</script>
<style lang="less" scoped>
@bmaincolor:v-bind(bmaincolor);
@font-size:24px;
@margin-top: 30px;
.text{
    margin-left: 0;
    width: 900px;
    div{
        text-align: left;
        .title{
            margin-top: @margin-top;
            margin-bottom: 20px;
            background-image: -webkit-linear-gradient(left,@bmaincolor,transparent,transparent);
            border-radius: 36px;
            padding-left: 15px;
            font-size: @font-size;
            color: rgb(95, 100, 107);
        }
        .content{
            width: 100%;
            text-indent: 20px;
            word-break: break-all;
        }
        img{
            max-width: 95.8%;
            margin-bottom: 10px;
            border-radius: 8px;
            box-shadow: 0 1px 10px 0 rgb(212,217,225);
        }
        .text{
            .title{
                margin-top: @margin-top * 0.7;
                margin-bottom: 0;
                background: none;
                font-size: @font-size * 0.95;
            }
            .text{
                .title{
                    padding-left: 0;
                    margin-top: @margin-top * 0.5;
                    font-size: @font-size * 0.8;
                }
                .text{
                    .title{
                        margin-top: @margin-top * 0.3;
                        font-size: @font-size * 0.7;
                    }
                }
            }
        }
    }
}
</style>