<template>
    <theContent  v-if="sections" :datas="sections" @getTitle="getTitle" class="thecontent"/>
    <nav class="right-nav">
        <rightNav v-if="sections" :datas="sections" @getLi="getLi" class="bigul ban-select" :first="1"/>
    </nav>
</template>
<script>
import {ref,watch,onMounted,onUnmounted,onUpdated} from 'vue';

import theContent from './theContent.vue'
import rightNav from './rightNav.vue'

import {spreadArr} from '../hooks/spreadArr'

import axios from 'axios';

import { useStore } from 'vuex'

export default {
    name:"theContain",
    props:['brunch'],
    components:{theContent,rightNav},
    setup(props) {
        const store=useStore()
        let serverstate=store.state.serverstate
        
        let d=ref({})
        let a=()=>{
                axios({
                url:`${serverstate}${props.brunch.route}data`,
                method:'GET'}).then(res=>d.value=res.data)
            }
        a()
        //不能用reactive，否则响应式会出问题
        let sections=ref([])
        watch(d,()=>{
            sections.value=d.value.sections
        })
        
        // 获取右导航栏dom
        let lis=[]
        let getLi=e=>{
            lis=e
        }
        // 获取titledom
        let titles=[]
        let getTitle=e=>{
            titles=e
        }
        // 绑定滚动事件
        onMounted(()=>{
            window.addEventListener("scroll",Scroll);
        }) 
        onUnmounted(()=>{
            window.removeEventListener("scroll",Scroll);
        })

        // 递归取出title和li
        let truetitles=[]
        let truelis=[]
        onUpdated(()=>{
            spreadArr(titles,truetitles)
            spreadArr(lis,truelis)
        })
        let Scroll=()=>{
            truetitles.forEach((truetitle,k)=>{
                let twt=Math.floor(truetitle.getBoundingClientRect().top)
                // getBoundingClientRect().top值不连贯需作范围判定
                if(twt<=25&&twt>=-25){
                    //判断li是否已被点击
                    if(truelis[k].className!='active'){
                        truelis[k].click()
                    }
                }
            })
            //改变nav position
            if(window.pageYOffset>55)
            {
                position.value='fixed'
                top.value='20px'
            }
            else {
                position.value='absolute'
                top.value='75px'
            }
        }

        let position=ref('absolute')
        let top =ref('75px')

        return{
            sections,
            getLi,
            getTitle,
            position,
            top
        }
    }

}
</script>
<style lang="less" scoped>
    @position:v-bind(position);
    @top:v-bind(top);
    .thecontent{
        margin-bottom: 942px;
    }
    .right-nav{
        overflow:scroll;
        overflow-x:hidden;
        position: @position;
        top: @top;
        margin-left: 910px;
        width: 200px;
        max-height: 92vh;
        &::-webkit-scrollbar {
            display: none;
            width: 4px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background: rgba(0,0,0,0.2);
        }
        &::-webkit-scrollbar-track {
            border-radius: 0;
            background: rgba(0,0,0,0.1);
        }
        &:hover{
            &::-webkit-scrollbar{
                display: block;
            }
        }
    }
</style>